<template>
  <div class="container">
    <h2>Computers</h2>

    <app-alert
      :message="message"
    ></app-alert>

    <computer-form v-if="isAuth" />

    <app-computers-list
      :computers="getComputers"
    ></app-computers-list>

  </div>
</template>

<script>
  import Rest from '../rest'
  import AppComputersList from '@/components/AppComputersList'
  import ComputerForm from '@/components/ComputerForm'
  import AppAlert from '@/components/AppAlert'
  import {mapGetters} from 'vuex'

  export default {
    data() {
      return {
        message: ''
      }
    },
    methods: {
      async loadComputers() {
        await this.$store.dispatch('loadComputers', '/computers')
      }
    },
    computed: {
      ...mapGetters(['getComputers']),
      isAuth() {
        return this.$store.getters['auth/isAuthenticated']
      }
    },
    async mounted() {
      await this.loadComputers()
    },
    components: {AppComputersList, ComputerForm, AppAlert}
  }
</script>

<style scoped lang="stylus">
  .form-grid
    display flex
    flex-wrap wrap

  .field
    min-width 25%

</style>
