<template>
  <div v-if="cabinets.length !== 0">
    <table class="table">
      <tr>
        <th>Number:</th>
        <th>Floor:</th>
        <th>Department:</th>
      </tr>
      <tr v-for="cabinet in cabinets" :key="cabinet.number">
        <td>
          <router-link :to="`/cabinet/${cabinet.number}`">{{cabinet.number}}</router-link>
        </td>
        <td>{{cabinet.floor}}</td>
        <td>{{cabinet.department}}</td>
        <td v-if="isAuth">
          <button
            @click="$emit('editCabinet', cabinet.number)"
          >Edit
          </button>
          <button @dblclick="$emit('deleteCabinet', cabinet.number)">Delete</button>
        </td>
      </tr>
    </table>
  </div>

  <div v-else>List is empty</div>
</template>

<script>
  export default {
    emits: ['editCabinet'],
    props: ['cabinets', 'isEdit'],
    computed: {
      isAuth() {
        return this.$store.getters['auth/isAuthenticated']
      }
    }
  }
</script>

<style scoped lang="stylus">
  table
    width: 100%

  .buttons
    display: flex;

  .card
    display: flex;
    align-items: center
    flex-direction row
    justify-content: space-between;
</style>
