<template>
  <div v-if="message" :class="['alert', message.type]">
    {{ message.value }}
  </div>
</template>

<script>
  import {computed} from 'vue'
  import {useStore} from 'vuex'

  export default {
    setup() {
      const store = useStore()

      const message = computed(() => store.state.message)

      return {
        message
      }
    }
  }
</script>

<style scoped lang="stylus">
.alert
  position fixed
  z-index 9999
  right: 64px
  top: 128px
  max-width 300px
</style>
